body {
	margin: 0;
	box-sizing: border-box;

	font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
	font-weight: bold;
}

.people {
	display: grid;
	grid-template-columns: repeat(2, 200px);
	justify-content: center;
	row-gap: 0;
	column-gap: 30px;
	
	margin: 30px 35px;

	@media (min-width:750px) {
		margin: 30px 160px;
	}
	@media (min-width:1000px) {
		grid-template-columns: repeat(4, 200px);
		margin: 30px 55px;
	}
	@media (min-width:1500px) {
		margin: 30px 305px;
	}
}
.person {
	position: relative;
	
	width: 220px;
	height: 287px;
	border-radius: 10px;
	background-color: transparent;
	transition: 1s;

	&:hover {
		background-color: #eee;
		transform: scale(1.2); 
		z-index: 1;

		.extra {
			color:#000;
		}

		.athlet {
			border-radius: 60px;
			transition-duration: 1s;
		}
	}
}

.rank {
	position: absolute;
	top: 94px;
	right: 20px;

	display: flex;
	align-items: center;
	justify-content: center;

	width: 32px;
	height: 32px;
	border-radius: 50%;
	background-color: #fff;

	font-size: 16px;
	color: #8b0000;
}
.details {
	margin: 0 auto;
	padding-top: 218px;
}
.name {
	display: flex;
	justify-content: center;

	color: #000;
	font-size: 20px;	
	line-height: 100%;
}
.extra {
	margin-top: 9px;
	display: flex;
	justify-content: center;
	transition: .3s;

	font-weight: normal;
	font-size: 18px;
	color: transparent;
}
.sport {
	&::after {
		content: ',';
	}
}
.pay {
	&::before {
		content: '$';
	}

	&::after {
		content: 'M';
	}
}
.athlet {
	position: absolute;
	top: 10px;
	left: 10px;

	width: 200px;
	height: 200px;
	border-radius: 50%;

	transition-delay: 1s;
	transition: 1s;
}
